.box{
  position: relative;
  max-width: 30%;
  max-height: 30%;
  width: 30%;
  height: 30%;
  min-width: 240px;
  min-height: 240px;
  margin: 1rem 1rem 0;
  background: #fff6da;
  box-shadow: 0 10px 40px -10px rgba(0,0,0,.5);
}
.templetelink{
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.flexname{
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}
.box:hover img{
  width: 0;
  height: 0;
  transition: 1s;
}
.flextitle{
  position: relative;
  margin-top: 1em;
  margin-left: 12px;
  color: #042f4b;
  z-index: 2;
}
.flextext{
  margin: 1em 12px 0;
  color: #042f4b;
  opacity: 0;
}
.box:hover p{
  opacity: 1;
  transition: .5s;
  transition-delay: 1s;
}
