.textwrapper {
  position: relative;
  width: 8rem;
  height: 3rem;
}
.textwrapper [class^="square"] {
  position: absolute;
  margin: 0;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
.textwrapper [class$="1"] {
  background-color: #2d132c;
}
.textwrapper [class$="2"] {
  background-color: #801336;
}
.textwrapper [class$="3"] {
  background-color: #c72c41;
}
.textwrapper [class$="3"] {
  background-color: #ee4540;
}
.textwrapper h1 {
  position: absolute;
  margin: 0;
  z-index: 2;
  color: white;
  font-size: 2rem;
}
