.homeheader{
  position: relative;
  width: 11rem;
  height: 3rem;
  margin: 2rem auto 0;
    [class^="crumb"] {
      position: absolute;
      margin: 0;
      transform: translate(-50%, -50%);
      z-index: 1;}
   [class$="1"] {
    background-color: #2d132c; }
   [class$="2"] {
    background-color: #801336; }
   [class$="3"] {
    background-color: #c72c41; }
   [class$="4"] {
    background-color: #ee4540; }
}
.homeheadertext {
 position: absolute;
 margin: 0;
 z-index: 2;
 color: white;
 font-size: 2rem;
}
